.wrap {
  overflow: hidden;
}
.btn {
  display: flex;
  justify-content: center;
  align-items: center;
}
.btn-red {
  background: #571919;
  border-radius: 16px;
  span {
    font-family: 'PlayfairDisplay';
    font-size: 16px;
    line-height: 120%;
    color: #ffffff;
  }
  &:hover {
    background: #7B2525;
  }
}
.btn-pink {
  background: #E6D6BF;
  border-radius: 16px;
  span {
    font-size: 16px;
    line-height: 120%;
    color: #571919;
  }
  &:hover {
    background: #F1D3A7;
  }
}
.text48 {
  font-size: 48px;
  line-height: 120%;
  font-weight: 400;
  color: #383838;
}
.swiper-button-prev {
  left: -30px;
  width: 60px;
  height: 60px;
  background: #FFFFFF;
  border-radius: 50%;
  transition: all 0.5s;
  &:after {
    display: none;
  }
  &:hover {
    background: #571919;
    svg path {
      stroke: #ffffff;
    }
  }
}
.swiper-button-next {
  right: -30px;
  width: 60px;
  height: 60px;
  background: #FFFFFF;
  border-radius: 50%;
  transition: all 0.5s;
  &:after {
    display: none;
  }
  &:hover {
    background: #571919;
    svg path {
      stroke: #ffffff;
    }
  }
}
.favourites {
  &--active {
    svg path {
      fill: #AB1F1F;
      stroke: #AB1F1F;
    }
  }
}
.navigation {
  padding: 50px 15px;
  &__list {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
    li {
      font-size: 16px;
      line-height: 120%;
      a:hover {
        color: #AB1F1F;
      }
    }
  }
}
.form {
  &-group {
    input {
      height: 52px;
      background: #FFF;
      border: 1px solid #9B9B9B;
      padding: 5px 12px;
      font-size: 20px;
      line-height: 120%;
      color: #000000;
      &::placeholder {
        color: #9b9b9b;
      }
      &:focus {
        box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.15) inset;
      }
    }
    textarea {
      height: 132px;
      background: #FFF;
      border: 1px solid #9B9B9B;
      padding: 12px;
      font-size: 20px;
      line-height: 120%;
      color: #000000;
      resize: none;
      &::placeholder {
        color: #9b9b9b;
      }
      &:focus {
        box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.15) inset;
      }
    }
  }
  &-checkbox {
    position: relative;
    cursor: pointer;
    input {
      position: absolute;
      z-index: 2;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      cursor: pointer;
      opacity: 0;
      &:checked ~ label {
        &:before {
          opacity: 1;
          pointer-events: all;
        }
      }
    }
    label {
      font-size: 16px;
      line-height: 120%;
      color: #9B9B9B;
      position: relative;
      padding-left: 25px;
      a {
        position: relative;
        z-index: 3;
        text-decoration-line: underline;
        text-underline-offset: 3px;
      }
      a:hover {
        color: #AB1F1F;
      }
      &:after {
        content: '';
        position: absolute;
        top: 2px;
        left: 0;
        width: 16px;
        height: 16px;
        border: 1px solid #9B9B9B;
      }
      &:before {
        content: '';
        position: absolute;
        top: 50%;
        left: 3px;
        transform: translate(0, -50%);
        width: 10px;
        height: 6px;
        background-image: url("../img/check.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
        opacity: 0;
        pointer-events: none;
        transition: all 0.3s;
      }
    }
  }
}

@media screen and (max-width: 1350px) {
  .swiper-button-prev {
    left: 0;
  }
  .swiper-button-next {
    right: 0;
  }
}

@media screen and (max-width: 992px) {
  .text48 {
    font-size: 20px;
  }
}